@import "../_variables";

section.cool-shit {
	position: relative;
	//height: 780px;
    overflow: hidden;

	.items {
		padding: 0 7%;

		.item {
			position: relative;
			display: block;
			text-align: center;
			background-size: cover;
    		background-position: center center;
    		margin-bottom: 30px;
			height: 250px;

			.overlay {
				padding:10px;
				opacity:0;
				transition: opacity 500ms;
			}

			.caption {
				color: white;
			    position: absolute;
			    top: 20px;
			    left: 20px;
			    bottom: 91px;
			    right: 10px;
			    overflow: hidden;
			}

			.likes {
				position: absolute;
				bottom: 15px;
				left: 15px;
			}

			&:hover {
				opacity:1;
				.overlay {
					opacity:1;
				}
			}
		}
	}

	.see-more {
		padding: 30px;
	}
	
	.load-more {
		position: absolute;
	    bottom: 20px;
	    left: 50%;
	    transform: translateX(-50%);
	    z-index: 1000;
	}
}