@import "../_variables";

section.big-moments {
	.item {
		border-top: 0.5px $gray-darker solid;
		border-bottom: 0.5px $gray-darker solid;
		margin-bottom:30px;

		@media (max-width:$screen-xs-max) {
			border-bottom:none;
			margin-bottom: 0;
		}

		.subtitle {
			margin:0;
		}
		h2 {
			margin-top:0;
		}
		.content {
			height: auto;
		    font-size: 14px;
		    max-width: 656px;
		}


	}
	@media (max-width: 1024px) {
		.feature.split .item .bg {
		    width: 200%;
		    -webkit-transform: translateX(-50%);
		    -ms-transform: translateX(-50%);
		    transform: translateX(-50%);
		}
		.feature.split .item.right .bg {
		    width: 200%;
		    -webkit-transform: translateX(0%);
		    -ms-transform: translateX(0%);
		    transform: translateX(0%);
		}
		.feature.split .item {
			width: 100%;
			overflow: hidden;
			position: relative;
			left: 0;
			right: 0 !important;
			//padding: 5%;
			text-align: center !important;
			-webkit-transform: none;
			-ms-transform: none;
			transform: none;
			//padding-bottom: 80px;
			border-bottom: none;
    		margin-bottom: 0;
		}
		.feature.split .item .description {
			position: relative;
		    left: 0;
		    right: 0 !important;
		    padding: 5%;
		    text-align: center !important;
		    -webkit-transform: none;
		    -ms-transform: none;
		    transform: none;
		    .content {
			    margin-left: auto;
	    		margin-right: auto;
		    }
		    //padding-bottom: 80px;
	    }
		.feature .item .grad {
			display: none;
		}
	}
}