@import "../_variables";

section.reasons {

	.item {
		border-top: 0.5px $gray-darker solid;
		border-bottom: 0.5px $gray-darker solid;
		margin-bottom:30px;

		@media (max-width:$screen-md-max) {
			border-bottom:none;
			margin-bottom: 0;

			.title, h2 {
				font-size: 40px !important;
			}

			> .bg {
				width: 200%;
			    transform: translateX(-50%);
			}

			.description {
				position: relative;
				left:0 !important;
				right:0 !important;
				padding:5%;
				text-align: center !important;
				transform:none;
				padding-bottom: 80px;

				.bg {
					position: absolute;
					top:0;
					left:0;
					height:100%;
					display:block;
					z-index:-1;
					transform: translateX(0%);
					opacity:0.2;
				}
			}
			.grad {
				display:none;
			}

			&.right {
				> .bg {
					transform: translateX(0%);
				}

				.description {
					.bg {
						transform: translateX(-50%) !important;
					}
				}
			}

			.overlay, .arrow {
				display:none;
			}

			.description {
				transform:none !important;
				img.bg {
					transform: translateX(0%)!important;
    				width: 250%;
				}
			}

			&.right {
				.description {
					left:0 !important;
					img.bg {
						transform: translateX(-61%)!important;
	    				width: 260%;
					}
				}
			}
		}

		@media (max-width:$screen-md-max) {
			.description {
				h2 {
					font-size: 30px;
				}
			}
		}

		@media (max-width:$screen-lg-max) {
			.description {
				h2 {
					font-size: 40px;
					font-size: 3vw;
				}

				p {
					font-size: 15px;
				}
			}
		}

		.subtitle {
			margin:0;
		}
		h2 {
			margin-top:0;
		}

		.description {
			z-index:1000;
			font-size:16px;
			right:64%;
			left: 5%;
			bottom:50%;
			transform: translateY(50%);
		}

		.overlay {
			right:60.3%;
			background: rgba(0,0,0,0.8)
		}

		.arrow {
			@extend .arrow-right;
			position:absolute;
			left:39.68%;
			top:20%;
			border-left-color: rgba(0,0,0,0.8);
		}

		&.right {
			.overlay {
				left:60.3%;
				right:0;
			}

			.description {
				left:64%;
				right:5%;
			}

			.arrow {
				@extend .arrow-left;
				left:auto;
				right: 39.68%;
			}
		}
	}
}