@import "../_variables";

* {
    box-sizing: border-box;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    -webkit-tap-highlight-color: transparent;

    &:focus {
    	outline: none;
    }
}

body {
    font-family: $font-family-sans-serif;
    color: $white;
    background: $black;
    overflow-x: hidden !important;
    -webkit-font-smoothing: antialiased;
    text-shadow: 1px 1px 1px rgba(0,0,0,0.004);
	
	@media (max-width:$screen-xs-max) {
		padding-top:50px;
	}

}

img.svg,
svg {
    width: 50px;
}

a {
	cursor: pointer;
}

section {
	position: relative;
	width: 100%;
	overflow-x: hidden;

	&.fullscreen {
		padding:0;
	}
}

.radial-bg-gray {
	/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#b5b5b5+0,b5b5b5+50,b5b5b5+100&1+0,1+50,0+100 */
	background: -moz-radial-gradient(center, ellipse cover, rgba(70,70,70,.7) 0%, rgba(70,70,70,.7) 50%, rgba(70,70,70,0) 100%); /* FF3.6-15 */
	background: -webkit-radial-gradient(center, ellipse cover, rgba(70,70,70,.7) 0%,rgba(70,70,70,.7) 50%,rgba(70,70,70,0) 100%); /* Chrome10-25,Safari5.1-6 */
	background: radial-gradient(ellipse at center, rgba(70,70,70,.7) 0%,rgba(70,70,70,.7) 50%,rgba(70,70,70,0) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#b5b5b5', endColorstr='#00b5b5b5',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
}

.seperator-h {
	margin: 20px 0;

	.light, .dark {
		height:1px;
		width: 100%;
	}
	.light {
		background: rgba(110,110,110,1);
		/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#b5b5b5+0,666666+50,b5b5b5+100&0+0,1+51,0+100 */
		background: -moz-linear-gradient(left, rgba(110,110,110,0) 10%, rgba(110,110,110,0.98) 50%, rgba(110,110,110,.10) 100%); /* FF3.6-15 */
		background: -webkit-linear-gradient(left, rgba(110,110,110,0) 10%,rgba(110,110,110,0.98) 50%,rgba(110,110,110,.10) 100%); /* Chrome10-25,Safari5.1-6 */
		background: linear-gradient(to right, rgba(110,110,110,0) 10%,rgba(110,110,110,0.98) 50% 51%,rgba(110,110,110,.10) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
		filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00b5b5b5', endColorstr='#00b5b5b5',GradientType=1 ); /* IE6-9 */
	}
	.dark {
		/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#444444+0,444444+50,444444+100&0+0,1+51,0+100 */
		background: -moz-linear-gradient(left, rgba(50,50,50,0) 10%, rgba(50,50,50,0.98) 50%, rgba(50,50,50,.10) 100%); /* FF3.6-15 */
		background: -webkit-linear-gradient(left, rgba(50,50,50,0) 10%,rgba(50,50,50,0.98) 50%,rgba(50,50,50,.10) 100%); /* Chrome10-25,Safari5.1-6 */
		background: linear-gradient(to right, rgba(50,50,50,0) 10%,rgba(50,50,50,0.98) 50%,rgba(50,50,50,.10) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
		filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00444444', endColorstr='#00444444',GradientType=1 ); /* IE6-9 */
	}
	.square {
	    width: 30px;
	    height: 2px;
	    background: white;
	    margin: 0 auto;
	}
}

.arrow-right {
	width: 0; 
	height: 0; 
	border-top: 30px solid transparent;
	border-bottom: 30px solid transparent;
	border-right: 30px solid transparent;
	
	border-left: 30px solid rgba(0,0,0,0.8);
}

.arrow-left {
	width: 0; 
	height: 0; 
	border-top: 30px solid transparent;
	border-bottom: 30px solid transparent; 
	border-left: 30px solid transparent;
	border-right:30px solid rgba(0,0,0,0.8);
}

.social {
	a {
		transition: opacity 300ms;
		&:hover {
			opacity:.6;
		}
	}
	img {
		height:20px;
	}
}

.overlay {
	position: absolute;
	top:0;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: 999;

	&.dark {
		background: rgba(0,0,0,0.7);
	}
}

/* Hacks */

// Create a bootstrap row that works for 5 items
.col-xs-15,
.col-sm-15,
.col-md-15,
.col-lg-15 {
    position: relative;
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;
}

.col-xs-15 {
    width: 20%;
    float: left;
}
@media (min-width: $screen-sm) {
.col-sm-15 {
        width: 20%;
        float: left;
    }
}
@media (min-width: $screen-md) {
    .col-md-15 {
        width: 20%;
        float: left;
    }
}
@media (min-width: $screen-lg) {
    .col-lg-15 {
        width: 20%;
        float: left;
    }
}



/* Utilities */

.no-flex {
    display: block !important;
}

.centered {
    margin: 0 auto;
}

.inline-block {
    display: inline-block;
}

.noselect {
    // Disable all selections
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.nopointerevents {
	pointer-events: none;
}

img.fit-cover {
	object-fit: cover;
}

.fit-cover video {
	object-fit: cover;
}

/* Screen reader text */
.sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0,0,0,0);
    border: 0;
}