@import "../_variables";

.section-title {
	background: url('../images/title-bg.jpg') no-repeat;
	background-size: cover;
	width: 100%;
	height: 150px;
	overflow-x: hidden;
	position: relative;
	color: $gray-light;

	&.no-bg {
		background: none;
	}

	&.short {
		height:100px;
	}

	.content {
		position: absolute;
	    top: 50%;
	    left: 50%;
	    transform: translate(-50%,-20%);
	    width: 90%;
	}

	h3 {
		text-align: center;
		margin-top:0;
		color:$gray;
		font-size:22px;
	}

	hr {
		width: 30px;
		border-color: $gray;
	}
}