@import "../_variables";

section.services {

	h2 {
		font-size: 50px;
	}

	h5 {
		font-size: 16px;
	}

	.service {
		height: 120px;
		@media (max-width:$screen-xs-max) {
			padding:20px;

			h5 {
				font-size: 12px;
			}
		}

		img {
			height: 40px;
		}
	}

	@media (max-width:$screen-xs-max) {
		section.services .service {
			height: 120px;
		}
	}

	p {
		line-height:26px;
	}

	.seperator-h {
		margin:40px 0;
	}

	.btn-down-arrow {
		padding-top: 30px;
	}

}