@import "../_variables";

section.clients {

	.client-list {
		padding:0 7%;
	}

	.client {
		background: $gray-darkest;
		margin:20px 0;
		text-align: center;
		padding: 25px;

		@media (max-width:$screen-sm-max) {
			padding: 10px;
		}

		img {
			width: 100%;
			filter: grayscale(100%);
			opacity:.5;
			transition: all 500ms;
			display:block;

			@media (max-width:$screen-sm-max) {
				filter: grayscale(0);
				opacity:1;
			}
		}

		&:hover {
			img {
				filter: grayscale(0);
				opacity:1;
			}
		}
	}

}