@import "../_variables";

footer {
	background: url('../images/footer-bg.jpg') no-repeat;
	background-size:cover;
	min-height: 230px;
	padding:30px;
	border-top: 1px $gray-dark solid;

	@media(max-width:$screen-sm-max) {
		text-align: center;

		.location {
			padding-top:30px;
		}
	}

	.logo {
		height:55px;
		max-width: 100%;
	}

	h3 {
		margin-top:0;
		font-size: 16px;
	}

	a {
		font-size: 14px;
		text-transform: uppercase;
	}

	> .row {
		padding:0 7%;
	}
}