@import "../_variables";

section.awards {
	.item {
		border-top: 0.5px $gray-darker solid;
		border-bottom: 0.5px $gray-darker solid;
		margin-bottom:10px;
		position: relative;

		&:last-child {
			margin-bottom: 30px;
		}

		&.right {
			.desc {
				text-align:right;
			}
			.images {
			}
		}

		@media (max-width:$screen-xs-max) {
			border-bottom:none;
			margin-bottom: 0;
		}

		.radial-bg-gray {
			padding:0;
		}

		.btn {
			margin: 0 0 0 20px;
	
			span {
				font-size: 30px;
			}
		}

		.desc {
			padding: 30px;
		}

		.subtitle {
			margin:0;
			font-size:16px;
			color: $gray-light;
		}
		h2 {
			margin-top:0;
		}

		img {
			width:100%;
		}

		p {
			font-size: 14px;
		}
	}
}