@import "../_variables";

section.motivistas {

	.title {
		font-size:34px;
		margin-top:0;
	}

	p {
		color: $gray-light;
	}

	.photo {
		width:100%;
	}

	.social {
		margin-top: 30px;

		h4 {
			margin:0;
		}
	}

	@media (max-width:$screen-sm-max) {
		.text-right, .social {
			text-align: center;
		}
		
		.social {
			padding: 0 30% 30px;
		}

		.social .title {
			padding-bottom: 20px;
		}
	}

}