@import "https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300,700|Ruda|Open+Sans:700";

/* Colors */
/* Core Colors */
$black: #000000;
$white: #ffffff;
$orange: #ef4429;

/* UI Elements */
$gray-lighter: #EEE;
$gray-light: #cccccc;
$gray: #818285;
$gray-dark: #666;
$gray-darker: #444;
$gray-darkest: #1a1a1a;


// Make sure you add any new colors into this list
$colors: "black" $black, "white" $white, "orange" $orange, "gray-lighter" $gray-lighter, "gray-light" $gray-light, "gray" $gray, "gray-dark" $gray-dark, "gray-darker" $gray-darker, "gray-darkest" $gray-darkest;
$color-darken: 20%;

// Makes classes for each color
// ex. .red for red color, .red-bg for background color
// Add .active to get the active color
@each $i in $colors {
    .#{nth($i, 1)}-bg {
        background: nth($i, 2) !important;
    }

    .#{nth($i, 1)}-bg-dark {
        background: darken(nth($i, 2), $color-darken) !important;
    }

    .#{nth($i, 1)}-bg.touchable:active {
        background: darken(nth($i, 2), $color-darken) !important;
    }

    .#{nth($i, 1)} {
        color: nth($i, 2) !important;
    }

    // Icon colors
    svg.#{nth($i, 1)} {
        fill: nth($i, 2) !important;
    }
}

/* Typography */
$primary-font-weight: 600;


$font-family-headings: 'Open Sans Condensed', sans-serif;
.opensans {
	font-family: $font-family-headings;
}

$font-family-subheadings: 'Ruda', sans-serif;
$font-family-sans-serif: 'Ruda', sans-serif;
.ruda, .subtitle, .subheading {
	font-family: $font-family-subheadings;
	text-transform: uppercase;
}
.signika {
	font-family: $font-family-sans-serif;
}

$font-family-action: 'Open Sans Condensed', sans-serif;
.ptsans {
	font-family: $font-family-action;

}

$font-family-base: $font-family-sans-serif !default;

$font-size-base: 14px !default;
$font-size-large: ceil($font-size-base * 1.25) !default;

// ~18px
$font-size-small: ceil($font-size-base * 0.85) !default;

// ~12px

$font-size-h1: floor($font-size-base * 2.6) !default;

// ~36px
$font-size-h2: floor($font-size-base * 2.15) !default;

// ~30px
$font-size-h3: ceil($font-size-base * 1.7) !default;

// ~24px
$font-size-h4: ceil($font-size-base * 1.25) !default;

// ~18px
$font-size-h5: $font-size-base !default;
$font-size-h6: ceil($font-size-base * 0.85) !default;

// ~12px

//** Unit-less `line-height` for use in components like buttons.
$line-height-base: 1.42857 !default;

// 20/14
//** Computed "line-height" (`font-size` * `line-height`) for use with `margin`, `padding`, etc.
$line-height-computed: floor($font-size-base * $line-height-base) !default;

// ~20px

//** By default, this inherits from the `<body>`.
$headings-font-family: inherit !default;
$headings-font-weight: 700 !default;
$headings-line-height: 1.1 !default;
$headings-color: inherit !default;

/* Structure */
$header-height: 70px;
$progress-height: 12px;
$navbar-height: 12%;

/* Animations */
$anim-highlight: color 100ms, background 100ms;


// Media queries breakpoints

$screen-xs: 480px !default;
$screen-sm: 768px !default;
$screen-md: 992px !default;
$screen-lg: 1200px !default;

$screen-xs-max: ($screen-xs - 1) !default;
$screen-sm-max: ($screen-sm - 1) !default;
$screen-md-max: ($screen-md - 1) !default;
$screen-lg-max: ($screen-lg - 1) !default;