@import "../_variables";
@import"../atoms/buttons";

$description-offset: 10%;

.feature {
	@media (max-width:$screen-xs-max) {
		.item {
			margin-bottom: 0;

			.title, h2 {
				font-size: 40px !important;
			}

			.description {
				position: relative;
				left:0;
				right:0 !important;
				padding:5%;
				text-align: center !important;
				transform:none;
				padding-bottom: 80px;

				.bg {
					position: absolute;
					top:0;
					left:0;
					height:100%;
					display:block;
					z-index:-1;
					transform: translateX(0%);
					opacity:0.2;
				}
			}
			.grad {
				display:none;
			}

			&.right {
				.description {
					.bg {
						transform: translateX(-50%) !important;
					}
				}
			}
		}

		&.split {
			.bg {
				width: 200%;
    			transform: translateX(-50%);
			}

			.item.right {
				.bg {
					width: 200%;
    				transform: translateX(0%);
				}
			}
		}

		.wearemotive {
			top: 8%;
		    left: 50%;
		    width: 50%;
		    transform: translateX(-50%);
		}
	}

	@media (max-width:$screen-sm-max) {
		.item {
			.title, h2 {
				font-size: 30px;
			}
		}
	}

	.item {
		position: relative;
		display:block;
		font-size:0;
		color: $white;
		transition: all 800ms;

		&.right {
			.description {
				right: $description-offset;
				left: auto;
				text-align: right;
			}
			.grad {
				right:0;
				left:auto;
				opacity: .65;

				background: -moz-linear-gradient(left, rgba(0,0,0,0) 0%, rgba(0,0,0,1) 60%); /* FF3.6-15 */
				background: -webkit-linear-gradient(left, rgba(0,0,0,0) 0%,rgba(0,0,0,1) 60%); /* Chrome10-25,Safari5.1-6 */
				background: linear-gradient(to right, rgba(0,0,0,0) 0%,rgba(0,0,0,1) 60%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
				filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#a6000000',GradientType=1 ); /* IE6-9 */
			}

			&.item {
				&:hover {
					.grad {
						opacity: .9;
					}
				}
			}

		}

		&.reveal-on-scroll {
			transform: translateX(-5%);
			opacity:0;

			&.right {
				transform: translateX(5%);
			}

			&.revealed {
				transform:none;
				opacity:1;
			}
		}

		.shadow {
			background: -moz-linear-gradient(top,  rgba(0,0,0,0) 0%, rgba(0,0,0,1) 100%); /* FF3.6-15 */
			background: -webkit-linear-gradient(top,  rgba(0,0,0,0) 0%,rgba(0,0,0,1) 100%); /* Chrome10-25,Safari5.1-6 */
			background: linear-gradient(to bottom,  rgba(0,0,0,0) 0%,rgba(0,0,0,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
			filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#000000',GradientType=0 ); /* IE6-9 */

		    position: absolute;
		    bottom: 0;
		    width: 100%;
		    height: 45%;
		}
	}

	a.item {
		cursor:pointer;

		&:hover {
			opacity:1;

			.btn.small {
				@extend .btn-icon-hover;
			}
			.grad {
				opacity:.9;
			}
		}

		&:active {
	        opacity: .5;
	    }
	}

	.bg {
		width:100%;
		z-index:1;
	}

	.description {
		display:block;
		position:absolute;
		left: $description-offset;
		bottom: 12%;
		z-index: 5;

		.bg {
			display:none;
		}
	}

	h2 {
		font-size: 50px;
	}

	.subtitle {
		color: $white;
		font-size: 16px;
    	font-weight: bold;
    	text-shadow: 0px 0px 5px rgb(0,0,0);
    	text-transform: none;
    	font-weight: 700;
    	&.client {
    		color: $gray;
    	}
	}

	.btn {
		margin-top: 20px;
	}

	.grad {
		display:block;
		position:absolute;
		left:0;
		top:0;
		bottom:0;
		width:70%;
		transition: all 1000ms;

		opacity: .65;

		/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#000000+0,000000+100&0.65+0,0+100 */
		background: -moz-linear-gradient(left, rgba(0,0,0,1) 40%, rgba(0,0,0,0) 100%); /* FF3.6-15 */
		background: -webkit-linear-gradient(left, rgba(0,0,0,1) 40%,rgba(0,0,0,0) 100%); /* Chrome10-25,Safari5.1-6 */
		background: linear-gradient(to right, rgba(0,0,0,1) 40%,rgba(0,0,0,0) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
		filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#a6000000', endColorstr='#00000000',GradientType=1 ); /* IE6-9 */
	}

	.wearemotive {
		position: absolute;
	    z-index: 10;
	    bottom: 40%;
	    left: 10%;
	    width: 30%;
	}

	@media (max-width:$screen-xs-max) {
		.item {
			margin-bottom: 0;

			.title, h2 {
				font-size: 40px !important;
			}

			.description {
				position: relative;
				left:0;
				right:0 !important;
				padding:5%;
				text-align: center !important;
				transform:none;
				padding-bottom: 80px;

				.bg {
					position: absolute;
					top:0;
					left:0;
					height:100%;
					display:block;
					z-index:-1;
					transform: translateX(0%);
					opacity:0.2;
				}

				.subtitle {
					width: auto;
				}
			}
			.grad {
				display:none;
			}

			&.right {
				.description {
					.bg {
						transform: translateX(-50%) !important;
					}
				}
			}
		}

		&.split {
			.bg {
				width: 200%;
    			transform: translateX(-50%);
			}

			.item.right {
				.bg {
					width: 200%;
    				transform: translateX(0%);
				}
			}
		}

		.wearemotive {
			top: 8%;
		    left: 50%;
		    width: 50%;
		    transform: translateX(-50%);
		}
	}
}