@import "../_variables";

.about-head {
	.item {
		font-size:16px;
		background-size: cover;
		background-position: center center;
		min-height: 350px;

		.description {
			bottom: 15%;
		    left: 50%;
		    transform: translateX(-50%);
		    text-align: center;
		    width: 40%;

		    h2 {
		    	margin-top:0;
		    }

		    @media (max-width:625px) {
		    	width:100%;
		    	transform:none;
		    	left:0;
		    }

		    p {
		    	color: $gray-light;
		    }
		}
	}
	
}