.home-head-video {
	overflow: hidden;
	.home-logo {
		position: absolute;
		width: 38%;
		height: auto;
		z-index: 5;
		top: 50%;
		left: 50%;
		transform: translate3d(-50%, -50%, 0);
	}
}

.home-head {
	.carousel {
		.item {
			background-size: cover;
			background-position: center center;
		}
	}

	.home-carousel {
		.subtitle {
			width: 70%;
		}
	}
}