@import "../_variables";

.btn {
	@extend .noselect;
	font-family: $font-family-action;
	font-weight: 700;
	text-transform: uppercase;
    padding: 8px 22px;
    color: white;
    font-size: 18px;
    border: 1px solid $white;
    cursor: pointer;
    display: inline-block;
    transition: all 500ms;

    &:hover {
    	.svg .square1 {
			transform: rotate(90deg);
		}
		@-moz-document url-prefix() {
			.svg .square1 {
				transform: rotate(0deg);
			}
		}
    }
	
	span {
		overflow: hidden;
	    display: block;
	    white-space: nowrap;
	    transition: all 500ms;
	}

    &.with-icon {
    	position: relative;
    	padding-right: 64px;

    	&.small {
    		padding-right:32px;
    		span {
    			text-indent: -300px;
    		}

    		&:hover {
    			@extend .btn-icon-hover
    		}
    	}

    	&.small-fixed {
    		padding-right:32px;
    		span {
    			text-indent: -300px;
    		}
    	}
    }

    img, .svg {
    	position:absolute;
    	width: 20px;
    	right:16px;
    	top:50%;
    	transform: translateY(-50%);

    	.square1 {
    		transition: transform 400ms;
    		transform: rotate(0deg);
    		transform-origin: 50% 50%;
    	}
    }

    &:active {
        background-color: #666;
        opacity: 1;
        transition: all 200ms;
    }

    &.with-bg {
    	background: rgba(0,0,0,0.8);
    }
}

/* Seperated hover state for @extending */
.btn-icon-hover {
	padding-right: 64px;
	opacity: 1;

	span {
		text-indent: 0;
	}
	.svg .square1 {
		transform: rotate(90deg);
	}
	@-moz-document url-prefix() {
		.svg .square1 {
			transform: rotate(0deg);
		}
	}
}

.btn-arrow {
	cursor: pointer;

    @media (max-width:$screen-xs-max) {
		display:none;
    }

    &:hover {
    	.svg {
    		.square {
				transform: rotate(90deg);
	    	}
    	}

    	@-moz-document url-prefix() {
			.svg .square {
				transform: rotate(0deg);
			}
		}
    }

    .svg {
    	width:60px;
    	.square {
    		transition: transform 400ms;
    		transform: rotate(0deg);
    		transform-origin: 50% 50%;
    	}
    }

    &.carousel-arrow {
    	cursor:pointer;
    	
    	.svg {
    		width:30px;
    	}
    }

}

.btn-down-arrow {
	position: absolute;
    bottom: 0;
    padding: 10px 20px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 5;

    @media (max-width:$screen-xs-max) {
		display:none;
    }
}

.btn-block {
	position: static;
    transform: none;
    display: block;
}