@import "../_variables";

section.team {
	.people {
		margin-top: 30px;
	}

	.person {
		position: relative;
		margin-bottom:30px;



		img {
			width: 100%;
			border: 1px solid $gray-dark;
		}

		


		.details, .overlay {
			transition: opacity 400ms;
			opacity: 0;
		}

		.details {
			position: absolute;
			bottom:10%;
			left:0;
			right:0;
			text-align: center;
			z-index:1000;
			padding: 4px;

			h3 {
				font-size: 20px;
			}

			.subtitle {
				@extend .signika;
				text-transform: none;
				color: $gray-light;
			}
		}

		@media (max-width:768px) { 
			max-width: 400px;
			margin: 0 auto 30px;

			.details, .overlay {
				opacity:1;
			}

			.overlay {
				top: 80% !important;
			}

			.details {
				bottom: 3%;
			}
		}

		/* Hack for Ipad width */
		@media (width:768px) {
			.overlay {
				top: 70% !important;
			}

			.details {
				h3 {
					font-size: 2vw;
					margin-bottom: 0.2vw;
				}

				.subtitle {
					font-size: 1.5vw;
				}
			}
		}

		.overlay {
			top:1px;
			right:1px;
			left:1px;
			bottom:6px;
		}

		&:hover {
			.details, .overlay {
				opacity:1;
			}
		}
	}
}