@import "../_variables";
@import"../atoms/buttons";

.carousel {
	opacity:0;
	transition:opacity 1000ms;

	.carousel-arrow {
		position: absolute;
		z-index: 10;
		top: 50%;

		&.next {
			right: 2%;
		}

		&.prev {
			left: 2%;
		}
	}

	.item {
		float:left;
		.item-header {
			position: relative;
		}
	}

	&.slick-initialized {
		opacity:1;
	}

	/* If our items are in a carousel */
	.slick-dots {
		//position: absolute;
	    //bottom: 12%;
	    //left: 10%;
	    z-index: 3;
	    margin-bottom: 20px;
	    list-style-type: none;
    	padding: 0;

    	@media (max-width:$screen-xs-max) {
			position:static;
			margin-bottom: 10px;
			text-align: center;
    	}

    	li {
    		display: inline-block;
		    margin-right: 10px;
		    overflow: hidden;
		    height: 7px;
		    transition: background 500ms;

    		&.slick-active {
    			background: $orange;
    			border-color: $orange;
    			button {
	    			background: $orange;
	    			border-color: $orange;
	    		}
    		}

    		button {
    			text-indent: -999px;
			    width: 27px;
			    height: 7px;
			    background: transparent;
			    border: 1px solid white;
			    transition: background 500ms;
    		}
    	}
	}

	&.carousel-inner {
		.slick-dots {
			position: relative;
			left: auto;
			right: auto;
			button {
				height: inherit;
				position: relative;
				top: auto;
				right: auto;
			}
		}
	}
}