@import "../_variables";

.modal {
	position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 5;
    background-color: black;

    > button {
    	background: transparent;
	    border: none;
	    cursor: pointer;
	    height: 34px;

	    position: absolute;
	    z-index: 10;
	    top: 74px;
	    right: 2%;
    	&.close {
    		.icon {
    			position: relative;
		    	width:26px;
		    	height: 26px;
		    	-webkit-transform: rotate(0deg);
				-moz-transform: rotate(0deg);
				-o-transform: rotate(0deg);
				transform: rotate(0deg);
				-webkit-transition: .5s ease-in-out;
				-moz-transition: .5s ease-in-out;
				-o-transition: .5s ease-in-out;
				transition: .5s ease-in-out;
				cursor: pointer;

				span {
					display: block;
					position: absolute;
					height: 6px;
				    width: 100%;
				    border: 1px solid white;
				    /* border-radius: 9px; */
					opacity: 1;
					left: 0;
					-webkit-transform: rotate(0deg);
					-moz-transform: rotate(0deg);
					-o-transform: rotate(0deg);
					transform: rotate(0deg);
					-webkit-transition: .25s ease-in-out;
					-moz-transition: .25s ease-in-out;
					-o-transition: .25s ease-in-out;
					transition: .25s ease-in-out;

					&:nth-child(1) {
						top: 10px;
						width: 0%;
						left: 50%;
						border-width: 0;
					}

					&:nth-child(2) {
						top: 10px;
						-webkit-transform: rotate(45deg);
						-moz-transform: rotate(45deg);
						-o-transform: rotate(45deg);
						transform: rotate(45deg);
					}

					&:nth-child(3) {
						top: 10px;
						-webkit-transform: rotate(-45deg);
						-moz-transform: rotate(-45deg);
						-o-transform: rotate(-45deg);
						transform: rotate(-45deg);
					}

					&:nth-child(4) {
						top: 10px;
						width: 0%;
						left: 50%;
						border-width: 0;
					}
				}
    		}
    	}
    }

    > .item, > .carousel {
    	position:absolute;
    	top: 0;
	    bottom: 0;
	    left: 0;
	    right: 0;
	    overflow-y: auto;
	    overflow-x: hidden;
    }

    .item {
    	&.video {
	    	overflow: hidden;
	    	.overlay, .description {
				opacity: 0;
				transition: all 500ms;
				transition-delay: 4s;
		    }
	    }

	    &.show-overlay {
	    	.overlay, .description {
	    		opacity: 1;
	    		transition-delay: 0s;
	    	}
	    }
    }


    .overlay {
    	background: rgba(0,0,0,0.4);
    }

    video.bg {
    	width: auto;
    }

    video::-webkit-media-controls-fullscreen-button, audio::-webkit-media-controls-volume-slider, video::-webkit-media-controls-volume-slider {
	    display: none;
	}

    h2 {
    	font-size: 60px;
    	margin-top: 9px;
    }

    p {
    	font-size:14px;
    }

    .social {
    	padding-top: 10px;
    }

    .description {
    	z-index: 1000;
    }

    .details {
    	font-size: 16px;

    	.carousel {
    		margin-top:30px;
    		img {
    			width: 100%;
    		}

    		.slick-dots {
    			position: static;
			    text-align: center;
			    padding-top: 20px;
			    margin: 0;
    		}
    	}
    }

    .item a {
    	font-size: 16px;
    }

	@media (max-width:$screen-xs-max) {
		button.close {
			top: 55px;
			right: 29px;
		}
		.item {
			min-height: 320px;
			margin-top: 100px;
			.video-js {
				min-height: 171px !important;
				height: auto !important;
				position: relative;
				top: auto;
				left: auto;
				transform: none;
				.vjs-poster {
					display: none;
				}
				.vjs-tech {
					object-fit: contain;
					bottom: 0;
					top: auto;
					height: auto;
				}
			}
			.overlay {
				position: relative;
				display: none;
			}
			.description {
				position: relative;
				opacity: 1 !important;
				z-index: auto;
				margin-left: auto;
				margin-right: auto;
			}
		}
	}
}